import "./results.css";
import ResultsSlider from "./ResultsSlider/resultsSlider";
import useWindowDimensions from "../../../components/useWindowDimensions/WindowDimensions";

function ResultsInfo() {
  return (
    <div
      className="info"
      id="history"
      style={{
        backgroundImage: 'url("/imgs/backgrounds/bg-blue-opy.png")',
      }}
    >
      <img src="/imgs/_-removebg-preview 1.svg" alt="logotype" />
      <h3>
         
        <br /> 
        <br />  
      </h3>
      <div className="info-text-container">
        <p>
          25  2000        
           , , ,    
                
             .
        </p>
      </div>
      <button className="about-btn">
        <a href="https://minpros.gospmr.org/about/9785"></a>
      </button>
    </div>
  );
}

export default function Results() {
  const dimensions = useWindowDimensions();

  return (
    <div className="results" id="history">
      {dimensions.width > 1024 ? (
        <>
          <div className="results-slider">
            <ResultsSlider />
          </div>
          <ResultsInfo />
        </>
      ) : (
        <>
          <ResultsInfo />
          <div className="results-slider">
            <ResultsSlider />
          </div>
        </>
      )}
    </div>
  );
}
